//media querieswidth

$pcMedia:1200px;
$padMedia:991px;
$phoneMedia:767px;
$smallphoneMedia:575px;
$iphone6:414px;
$iphone5:374px;

/*mixinmixinmedia queriesmixin
media querieswidth*/

//
@mixin media-1200Max() {
      @media (max-width: $pcMedia) {
            @content;
      }
}
@mixin media-1200Min() {
      @media (min-width: $pcMedia) {
            @content;
      }
}
//
@mixin media-991Max() {
      @media (max-width:$padMedia) {
            @content;
      }
}
@mixin media-991Min() {
      @media (min-width:$padMedia) {
            @content;
      }
}
//
@mixin media-767Max() {
      @media (max-width: $phoneMedia) {
            @content;
      }
 }
@mixin media-767Min() {
      @media (min-width: $phoneMedia) {
            @content;
      }
 }
//
@mixin media-575Max() {
      @media (max-width: $smallphoneMedia) {
            @content;
      }
}
@mixin media-575Min() {
      @media (min-width: $smallphoneMedia) {
            @content;
      }
}
//iphone6
@mixin media-414Max() {
      @media (max-width: $iphone6) {
            @content;
      }
}
@mixin media-414Min() {
      @media (min-width: $iphone6) {
            @content;
      }
}
//iphone5
@mixin media-374Max() {
      @media (max-width: $iphone5) {
            @content;
      }
}
@mixin media-374Min() {
      @media (min-width: $iphone5) {
            @content;
      }
}